.product {
  flex: 6;
  padding: 20px;
}

.productTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.createProduct {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.productTop {
  display: flex;
}

.productTopLeft {
  flex: 2;
}

.productTopRight {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  align-items: center;
  flex: 1;
}

.productTopRight {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.productInfoTop {
  display: flex;
  align-items: center;
}

.productInfoImg {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.productInfoName {
  font-weight: 600;
}

.productInfoBottom {
  margin-top: 30px;
}

.productInfoItem {
  width: 150px;
  display: flex;
  justify-content: space-between;
  padding: 5px 0;
}

.productInfoValue {
  font-weight: 300;
  color: gray;
}

.productBottom {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.productForm {
  display: flex;
  justify-content: space-between;
}

.productFormLeft {
  display: flex;
  flex-direction: column;
}

.productFormLeft > label {
  margin-bottom: 10px;
  color: gray;
}

.productFormLeft > input {
  margin-bottom: 10px;
  border: none;
  padding: 5px;
  border-bottom: 1px solid gray;
}

.productFormLeft > select {
  margin-bottom: 10px;
}

.productUploadImg {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  margin-right: 20px;
}

.productFormRight {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.productUpload {
  display: flex;
  align-items: center;
}

.productIcon {
  cursor: pointer;
  color: teal;
}

.productButton {
  border: none;
  padding: 5px;
  border-radius: 5px;
  background-color: teal;
  color: white;
  font-weight: 600;
  cursor: pointer;
}
