.userList {
  flex: 6;
}

.userList {
  display: flex;
  align-items: center;
}

.userListImg {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}

.userListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: #3bb077;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}

.userListDelete {
  color: rgb(236, 12, 12);
  cursor: pointer;
}
